import React from "react";
import ReactDOM from "react-dom/client";
import PriceMyRide from "../components/PriceMyRide.jsx";

// Create or reuse a container
let container = document.getElementById("boat-chat-container");
if (!container) {
  container = document.createElement("div");
  container.id = "boat-chat-container";
  document.body.appendChild(container);
}

// Mount the component immediately
const root = ReactDOM.createRoot(container);
root.render(<PriceMyRide />);
